@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
:root {
  --primary-white: #fff;
}

body {
  font-family: "Roboto Condensed", sans-serif;
}

/*--- navbar ---*/
nav {
  z-index: 2;
}

.navbar {
  padding: 0.5rem;
}

.logo {
  height: 3rem;
}

.navbar-brand {
  padding: 0.3125rem 1rem 0.3125rem 1rem;
  margin: 0;
}

.nav-link {
  letter-spacing: 0.0375rem;
  cursor: pointer;
  margin-left: 0.75rem;
  margin-right: 0.75rem;
  text-transform: uppercase;
  font-size: 0.9rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid transparent;
}

.nav-link:hover {
  border-top: 0.1875rem solid rgb(141, 223, 133);
}

.nav-link:focus,
.navbar-nav > li > a .active {
  border-top: 0.1875rem solid rgb(141, 223, 133);
}

/*--- header ---*/
.header-wrapper {
  overflow: hidden;
  position: relative;
  /* background: url(./images/bg4.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  background-color: rgba(0, 0, 0, 0.24); */
  height: 90vh;
}

.main-background {
  position: absolute;
  -webkit-filter: brightness(70%);
          filter: brightness(70%);
}

.main-info {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.main-info > h1 {
  text-transform: uppercase;
  color: rgb(164, 232, 255);
}

.typed-text {
  font-size: 2rem;
  color: white;
}

.header-button {
  border: 1px solid rgb(164, 232, 255);
  text-transform: uppercase;
  border-radius: 0%;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: white;
  margin: 2rem;
  text-decoration: none;
}

.header-button:hover {
  text-decoration: none;
}

/*--- particles ---*/
.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}

@media (max-width: 768px) {
  .particles {
    display: none;
  }

  /* body {
    font-size: 80%;
  } */
}

.my-intro {
  padding-top: 3rem;
  padding-left: 5rem;
  padding-right: 5rem;
  position: relative;
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
}

.my-intro > img {
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  margin: auto;
}

/* Footer */

.footer{
  padding: 60px 0;
  margin: 0;
  max-width: 100%;
}

.footer > h5 {
  padding-left: 10px;
  border-left: 3px solid #eeeeee;
  padding-bottom: 6px;
  margin-bottom: 20px;
}

.footer > a {
  text-decoration: none;
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

.footer ul.social li {
  padding: 3px 0;
}

.footer ul.social li a i {
  margin-right: 5px;
  font-size: 25px;
  transition: 0.5s all ease;
}

.footer ul.social li:hover a i {
  font-size: 30px;
  margin-top: -10px;
}

.footer ul.social li a,
.footer ul.quick-links li a {
  color: #ffffff;
  text-decoration: none;
}

.footer ul.social li a:hover {
  color: #eeeeee;
}

.footer ul.quick-links li {
  padding: 3px 0;
  transition: 0.5s all ease;
}

.footer ul.quick-links li:hover {
  padding: 3px 0;
  margin-left: 5px;
  font-weight: 700;
}

.footer ul.quick-links li a i {
  margin-right: 5px;
}

.footer ul.quick-links li:hover a i {
  font-weight: 700;
}

.contact-me {
    margin-top: auto;
    margin-bottom: auto;
    padding-right: 100px;
}

@media (max-width: 768px) {
    .footer h5 {
        padding-left: 0;
        border-left: transparent;
        padding-bottom: 0px;
        margin-bottom: 10px;
    }
    .contact-me {
        padding-right: 0;
    }
}


.cards {
  /* padding: 4rem; */
  background: #fff;
}

h1 {
  text-align: center;
  margin-top: 45px;
}

.cards__container {
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 1120px;
  width: 90%;
  margin: 0 auto;
}

.cards__wrapper {
  position: relative;
  margin: 50px 0 20px;
  max-width: 1000px;
}

.cards__items {
  margin-bottom: 24px;
}

.cards__item {
  display: flex;
  flex: 1 1;
  margin: 0 1rem;
  border-radius: 10px;
}

.cards__item__link {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}

.cards__item__pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.fade-img {
  -webkit-animation-name: fade-img;
          animation-name: fade-img;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}

.cards__item__pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 12px;
  font-weight: 700;
  color: #fff;
  background-color: #1f98f4;
  box-sizing: border-box;
}

.cards__item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}

.cards__item__img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.cards__item__info {
  padding: 20px 30px 30px;
}

.cards__item__text {
  color: #252e48;
  /* font-size: 18px; */
  line-height: 24px;
}

